'use strict';

var numeral = require('numeral');
var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;
var MapViewActions = require('../actions/mapViewActions');
var MetaActions = require('../actions/pageMetaActions');
var CountryActions = require('../actions/countryActions');
var CountryStore = require('../stores/countryStore');
var PointLayer = require('./map/pointLayer.jsx');
var Popup = require('./map/popup.jsx');
var Header = require('./header.jsx');
var NotYet = require('./notYet.jsx');


var CountryTotal = React.createClass({displayName: "CountryTotal",
  render: function() {
    var formatter = this.props.unit === 'USD' ? '$0,0' : '0,0';
    var amount = numeral(this.props.amount).format(formatter);
    return (
      React.createElement("tr", {className: "total"}, 
        React.createElement("th", null, this.props.title), 
        React.createElement("td", null, 
          React.createElement("span", {className: "amount"}, amount)
        )
      )
    );
  },
});


var ListedCountry = React.createClass({displayName: "ListedCountry",
  render: function() {
    return (
      React.createElement("li", null, 
        React.createElement(NotYet, null, 
          this.props.name
        )
      )
    );
  }
});


var International = React.createClass({displayName: "International",

  mixins: [Reflux.connect(CountryStore, 'countries')],

  getInitialState: function() {
    return {
      countryListOpen: false,
    };
  },

  componentWillMount: function() {
    CountryActions.load.triggerPromise()
      .then(MapViewActions.resetBounds);
  },

  componentDidMount: function() {
    MetaActions.setTitle('International');
  },

  pin: function(featureData, latlng) {
    var size; // size for divIcon
    var min_size = 10;
    var max_size = 64;
    var range_size = max_size - min_size;

    // HACK FOR DEMO: hardcoded ranges should be computed
    var min_commitments = 4000000;
    var max_commitments = 14885996000;
    var min_disbursements = 33033.00;
    var max_disbursements = 7570355696.03;

    var min_$ = Math.min(min_commitments, min_disbursements);
    var max_$ = Math.max(max_commitments, max_disbursements);
    var range_$ = max_$ - min_$;

    var comparison =  this.props.query.compare || "commitments";
    var amount = featureData.properties.totals[comparison].amount;

    size = (amount - min_$) / range_$ * range_size + min_size;

    if (size < min_size) {
      console.warn('boop');
      size = min_size;
    }
    if (size > max_size) {
      console.warn('beep');
      size = max_size;
    }

    return L.marker(latlng, {
      icon: L.divIcon({
        className: 'country-location-marker',
        iconSize: [size, size],
        iconAnchor: [size/2, size/2],
      }),
      title: featureData.properties.name,
      alt: 'Clickable map marker for ' + featureData.properties.name,
      riseOnHover: true
    });
  },

  popup: function(country) {
    var d = country.properties;
    return (
      React.createElement(Popup, {className: "country-popup"}, 
        React.createElement(Popup.Header, null, 
          React.createElement("h3", {className: "panel-title totals"}, 
            React.createElement(Link, {to: "country", params: {countryId: country.id}}, 
              React.createElement("img", {className: "flag", height: "48", width: "48", src: 'images/flags/' + country.id + '.png'}), 
              d.name
            )
          ), 
          React.createElement(Link, {to: "country", params: {countryId: country.id}}, 
            "Country details..."
          )
        ), 

        React.createElement("div", {className: "panel-body totals"}, 
          React.createElement("p", {className: "country-summary"}, "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."), 
          React.createElement("table", {className: "table table-condensed country-overview-data"}, 
            React.createElement("tbody", null, 
              React.createElement(CountryTotal, React.__spread({title: "Total Commitments"},  d.totals.commitments)), 
              React.createElement(CountryTotal, React.__spread({title: "Total Disbursements"},  d.totals.disbursements)), 
              React.createElement(CountryTotal, React.__spread({title: "Total Projects"},  d.totals.projects))
            )
          ), 
          React.createElement("div", {className: "to-country clearfix"}, 
            React.createElement(Link, {className: "pull-right", to: "country", params: {countryId: country.id}}, 
              "See country details"
            )
          )
        )
      )
    );
  },

  dblclick: function(feature, layer) {
    MetaActions.transitionTo('country', {countryId: feature.id});
  },

  toggleCountryList: function(e) {
    e && e.preventDefault();
    this.setState({countryListOpen: !this.state.countryListOpen});
  },

  render: function() {
    var countries = this.state.countries.features.sort(function(a, b) {
      return a.properties.name < b.properties.name ? -1 : 1;
    });

    return (
      React.createElement("div", null, 
        React.createElement(Header, null, 
          React.createElement(Header.Main, null, 
            React.createElement(Header.Dropdown, {
              className: 'country-chooser active' + (this.state.countryListOpen ? ' open' : ''), 
              onClick: this.toggleCountryList, 
              label: (
                React.createElement("span", {className: "country-choice"}, 
                  React.createElement("span", {className: "title"}, "Sovereign Projects"), 
                  React.createElement("span", {className: "sep"}, " - "), 
                  React.createElement("span", {className: "tip"}, "(Choose a Country)")
                )
              )}, 
              React.createElement(NotYet, {className: "select-all"}, "Select All / None"), 
              React.createElement("ul", {className: "country-list"}, 
                countries.map(function(country) {
                  return React.createElement(ListedCountry, {
                    key: country.id, 
                    id: country.id, 
                    name: country.properties.name})
                })
              ), 
              React.createElement("div", {className: "compare"}, 
                React.createElement("div", {className: "compare-title"}, 
                  React.createElement("span", {className: "compare-type"}, 
                    this.props.query.compare === 'disbursements' ? 'Disbursements ' : 'Commitments ', 
                    "Comparison"
                  ), 
                  React.createElement("span", {className: "compare-hint"}, " (limit 6 countries)")
                ), 
                React.createElement("em", null, "No countries selected")
              ), 
              React.createElement("div", {className: "apply"}, 
                React.createElement(NotYet, {className: "btn btn-link apply-button pull-right"}, 
                  React.createElement("span", {className: "glyphicon glyphicon-globe"}), " Apply"
                )
              )
            )
          ), 
          React.createElement(Header.Aux, null, 
            React.createElement(Header.Item, null, 
              React.createElement(NotYet, null, "#Loans/Grants")
            ), 
            React.createElement(Header.Item, {active: !this.props.query.compare || (this.props.query.compare === 'commitments')}, 
              React.createElement(Link, {
                to: "international", 
                query: {compare: 'commitments'}}, "Commitments")
            ), 
            React.createElement(Header.Item, {active: this.props.query.compare === 'disbursements'}, 
              React.createElement(Link, {to: "international", query: {compare: 'disbursements'}}, "Disbursements")
            )
          )
        ), 
        React.createElement(PointLayer, {
          getMap: this.props.getMap, 
          geojson: this.state.countries, 
          pin: this.pin, 
          popup: this.popup, 
          popupMaxWidth: 320, 
          compareTrigger: this.props.query.compare, //assigning the comparison to a prop makes page re-render after a comparison link is clicked
          dblclick: this.dblclick})
      )
    );
  }

});


module.exports = International;
